import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { UserService,LoggedInService,SubDomainService,CookieService } from './../services/index';
import { EmailValidator } from './../validators/email.validator';
import { environment } from './../../../environments/environment';

declare var jQuery:any;

@Component({
	selector: 'og-invitedlogin',
	templateUrl: './invitedLogin.component.html',
	styleUrls: ['./invitedLogin.component.css','./bootstrap-material/css/bootstrap-material-design.css','./bootstrap-material/css/ripples.min.css']
})

export class InvitedLoginComponent implements OnInit {
  loginForm: FormGroup;
  error: Boolean = false;
  ErrorMessageIsVisible:Boolean = false;
  ErrorMessage:String;

  constructor(
    private fb:FormBuilder,
    private _userService:UserService,
    public router: Router,
    private loggedInSerivce: LoggedInService,
    private subDomianService : SubDomainService,
    private _cookieService : CookieService
    ) {}

  ngOnInit() {
    jQuery('#login').removeClass('hide');
    jQuery('#login').modal('show');
    this.loginForm = this.fb.group({
      email: ['', Validators.compose([
        Validators.required, Validators.minLength(3), EmailValidator.format
      ])],
      password: ['', Validators.compose([
        Validators.required
      ])]
    });
    jQuery.material.init();
  }

  showErrorMessage() {
    this.ErrorMessageIsVisible = true;
  }

  hideErrorMessage() {
    this.ErrorMessageIsVisible = false;
  }

  onSubmit(value: any) {
		value = this.loginForm.value;
    jQuery('#loginSubmit').html('Please wait...');
    let link = window.location.hostname;
    let linkArray = link.split('.');
    let companyName : String = null;
    if(linkArray.length === 3 && linkArray[0] !== 'app')
      companyName = linkArray[0];
    this._userService.login(value.email, value.password,companyName)
      .subscribe(
        (response: any) => {
          console.log(response);
          if(response.token) {
            let storage = {
                'token' : response.token,
                'user'  : response.user
            };
            if(response.user.role === 'ADMIN') {
              console.log('Admin');
              this.router.navigate(['/admin/users']);
            }
            jQuery('#loginSubmit').html('Login');
            jQuery('#login').modal('hide');
            console.log(this.ErrorMessageIsVisible);
            // localStorage.setItem('storage',JSON.stringify(storage));
            this._cookieService.createCookie('storage',JSON.stringify(storage),3);
            if(this.subDomianService.subDomain.is_sub_domain_url) {
              // let win = (<HTMLIFrameElement>document.getElementById('mainUrlIframe')).contentWindow;
              // win.postMessage(JSON.stringify({key: 'storage', method: 'set',data : storage}), '*');
            }
            this.loggedInSerivce.login();
            this._userService.token = response.token;
            let url = response.user_company.company.sub_domain+'.'+environment.APP_EXTENSION;
            if(!this.subDomianService.subDomain.is_sub_domain_url)
              jQuery(location).attr('href','http://'+url);
          }
				},
        (response:any) => {
          jQuery('#loginSubmit').html('Login');
          this.error = true;
          console.log(response);
          this.ErrorMessage = response.error.message;
          console.log(response.error.message);
          this.showErrorMessage();
        }
      );
  }

  signUp() {
    console.log('okkk');
    jQuery('#login').modal('hide');
    jQuery('#signUp').modal('show');
    jQuery('.slide1 ').removeClass('hide');
    jQuery('.slide2 ').addClass('hide');
  }
}



